<script>
import Swiper from 'Swiper';
import {dotdotdot} from '../../static/js/jquery.dotdotdot.js'
var swiper,hList=[],g=0;
export default {
	data(){
    return{
    	totalList: [],
    	//学院动态
    	dynList:[],
    	//gongjian
    	noticeList:[],
    	//employment,
    	empList:[],
    	//student
    	stuList:[],
    	lunList:[],
    	hList:[],
    	teacherList:[],
    	bigList:[
    		{ img : '../../static/img/big1.jpg' },
    		{ img : '../../static/img/big2.jpg' },
    		{ img : '../../static/img/big3.jpg' },
    		{ img : '../../static/img/big4.jpg' },
    		{ img : '../../static/img/big5.jpg' },
    		{ img : '../../static/img/big6.jpg' },
    		{ img : '../../static/img/big7.jpg' },
    		{ img : '../../static/img/big8.jpg' },
    	]
    }
	},
	created:function(){
    var _self = this;
　　var url="http://api.7171.la/api/article";            
	  this.$http.post(url,{token:'ahlhsw',code:'6c9d1f7505ecf9e34e5ffd69f8b3c5ff',type:'34,35,36,37'}
		).then(function (data) {
       	var json = data.data;
		  	_self.totalList = json;
				for (var i = 0;i<_self.totalList.length;i++) {
					if(_self.totalList[i].type == "34"){
						if(_self.dynList.length<7){
							_self.dynList.push(_self.totalList[i]);
						}
					}
					if(_self.totalList[i].type == "35"){
						if(_self.noticeList.length<4){
							_self.noticeList.push(_self.totalList[i]);
						}
					}
					if(_self.totalList[i].type == "36"){
						if(_self.empList.length<7){
							_self.empList.push(_self.totalList[i]);
						}
					}
					if(_self.totalList[i].type == "37"){
						if(_self.stuList.length<6){
							_self.stuList.push(_self.totalList[i]);
						}
					}
					_self.$nextTick(() => {
						$('.news-title span').dotdotdot();
					});
				}
    	});
    	var url1="http://api.7171.la/api/banner";            
		  this.$http.post(url1,{token:'ahlhsw',code:'6c9d1f7505ecf9e34e5ffd69f8b3c5ff'}
			).then(function (data) {
        	var json = data.data;
			  	_self.lunList = json;
					for(var i=0;i<_self.lunList.length;i++){
						hList.push(_self.lunList[i].title);
					}
					_self.$nextTick(() => {
						_self.lunbo();				
					});
    	});     
    	var url2="http://api.7171.la/api/teacher";            
		  this.$http.post(url2,{token:'ahlhsw',code:'6c9d1f7505ecf9e34e5ffd69f8b3c5ff'}
			).then(function (data) {
        	var json = data.data;
					for (var i = 0;i<4;i++) {
						_self.teacherList.push(json[i]);
					}
					_self.$nextTick(() => {
						$(".tea-box >div span").dotdotdot();		
					});
    	});
    },
    methods: {
	    forward: function(n) {
	    	swiper.slideTo(n, 1000, false);
	    },
	    lunbo(){
	    	function active(n){
		  	 	$(".index").find("span").removeClass("active");
		  	 	$(".index").find("span").eq(n).addClass("active");
		  	 	$(".pagination h5").text(hList[n]);
		  	}
	    	swiper = new Swiper('.lunbo', {
            pagination: '.swiper-pagination',
		        paginationClickable: true,
		        nextButton: '.swiper-button-next',
        		prevButton: '.swiper-button-prev',
		        loop:'true',
		        lazyLoading : true,
		        autoplay : 6000,
	            onTouchEnd: function(swiper){
			      active(swiper.realIndex);
			    },
			    onSlideChangeEnd: function(swiper){
				    active(swiper.realIndex);
			    }
	        });
	        $(".lunbo-next,.lunbo-prev").click(function(){
	        	active(swiper.realIndex);
	        });
	        $(".pagination span").click(function(){
		  	 	$(".index").find("span").removeClass("active");
		  	 	var num = $(this).index();
		  	 	swiper.slideTo(num+1, 1000, false);
		  	 	active(num);
		  	});
	    },
	    select(uri){
	    	this.$router.push({ path: uri });
	    },
	  }, 
	  activated(){
    	document.documentElement.scrollTop = 0
    },
    mounted(){
    	var galleryTop = new Swiper('.gallery-top', {
        spaceBetween: 10,
        loop:true,
        loopedSlides: 3, //looped slides should be the same     
	    });
	    var galleryThumbs = new Swiper('.gallery-thumbs', {
	        spaceBetween: 10,
	        slidesPerView: 4,
	        touchRatio: 0.2,
	        loop:true,
	        loopedSlides: 3, 
	    });
	    galleryTop.params.control = galleryThumbs;
	    galleryThumbs.params.control = galleryTop;
	    $('.g-n').click(function(){
	    	event.stopPropagation();
				galleryTop.slideNext();
			})
			$('.g-p').click(function(){
				event.stopPropagation();
				galleryTop.slidePrev();
			})
	    $(".campus-box >div,.thumb").click(function(){
	    	$('.mask').css('visibility','visible');
	    });
	    $(".gallery-thumbs .swiper-slide").click(function(){
	    	event.stopPropagation();
	    	var ind = $(this).index();
	    	galleryTop.slideTo(ind, 1000, false);//切换到第一个slide，速度为1秒
	    });
	    $('.mask').click(function(){
	    	$('.mask').css('visibility','hidden');
	    });
    }
}
</script>

<style>
	.center{
		margin: 27px auto;
		overflow: hidden;
	}
	.construction >div{
    width: 100%;
    padding: 0 15px;
	}
	.tea-box >div span{
		display: block;
		height: 40px;
	}
	.mask{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0,0,0,0.8);
		z-index: 2;
		min-width: 320px;
		visibility: hidden;
	}
	.mask > div {
	    position: fixed;
	    top: 10%;
	    left: 5%;
	    width: 90%;
	}
	.mask img{
		display: block;
		margin: 0 auto;
	}
	.gallery-thumbs{
		margin-top: 15px;
	}
	.gallery-thumbs .swiper-slide {
    width: 25%;
    height: 100%;
    opacity: 0.7;
	}
	.gallery-thumbs .swiper-slide-active {
	    opacity: 1;
	}
	@media only screen and (max-width: 1024px) {
		.mask > div{
			top: 20%;
		}
	}	
	@media only screen and (max-width: 414px) {
		.mask > div{
			top: 25%;
		}
	}	
</style>



